import React, { Component } from 'react';
import { Select, Input, } from 'antd';
import Apis from '@/apis'
const InputGroup = Input.Group;
const { Option } = Select;


class View extends Component {

  constructor(props) {
    super(props)
    this.state = {
      options: []
    }
  }
  componentDidMount(){
    if(Apis['pageList']){
      Apis['pageList']({
        appid: window.localStorage.getItem('appid')
      }).then(res => {
        res.data.forEach(item => {
          item.key = item.id
          item.title = item.title || item.name
        })
        this.setState({
          options: res.data
        })
      })
    }
  }
  handleChange = val => {
    const { onChange } = this.props
    onChange && onChange(val)
  }

  render () {
    const { options } = this.state
    const { value, ...props } = this.props
    return (
      <InputGroup compact>
        <Input style={{ width: '60%' }} value={value} {...props} onChange={this.handleChange} />
        <Select 
          style={{ width: '40%' }} 
          placeholder="选择页面" 
          value={value}
          onChange={this.handleChange}
        >
        {
          options.map(item => <Option value={`/${item.key}`} key={item.key}>{item.title}</Option>)
        }
        </Select>
      </InputGroup>
    )
  }
}
export default View